/* global L */
import handlebars from 'handlebars/runtime';
import { u } from 'umbrellajs/umbrella';
import BaseComponent from '~global-js/component/base-component';
import * as actions from '~components/map/js/actions';
import { isSmallScreen } from '~global-js/helpers/breakpoints';
import { event as analyticsEvent } from '~global-js/helpers/tracking';
import propertyPinTemplate from '~components/map/templates/property-pin.hbs';
import propertyPopupTemplate from '~components/map/templates/property-popup.hbs';
import universityPopupTemplate from '~components/map/templates/university-popup.hbs';
import { formatCurrency } from '~global-js/helpers/currency';
import zIndexes from '~global/variables/z-indexes.json';
import analyticsCategory from '~components/map/js/settings.js';

export default class MapComponent extends BaseComponent {
  map = {};
  mapLayers = {};

  static setPropertyMarkerAnalytics(marker) {
    marker.on('click', (event) => {
      analyticsEvent(analyticsCategory, 'cardOpenProperty', event.target.id);
    });
  }

  static getAnalyticsId(element) {
    const parent = u(element).closest('[data-id]').first();
    return parent && parent.getAttribute('data-id');
  }

  static handlePropertyClick(element) {
    if (!element.classList) {
      return;
    }

    if (element.classList.contains('map-popup__property_name')) {
      const id = MapComponent.getAnalyticsId(element);
      analyticsEvent(analyticsCategory, 'click', `name | ${id}`);
    }

    if (element.classList.contains('map-popup__property_price') || element.classList.contains('map-price')) {
      const id = MapComponent.getAnalyticsId(element);
      analyticsEvent(analyticsCategory, 'click', `fromPrice | ${id}`);
    }
  }

  handleUniversityClick(element) {
    const parentElement = element.parentNode;
    let selectUniElement;

    if (parentElement.classList && parentElement.classList.contains('js-map-set-uni')) {
      selectUniElement = parentElement;
    } else if (element.classList && element.classList.contains('js-map-set-uni')) {
      selectUniElement = element;
    }

    if (selectUniElement) {
      const uniSlug = selectUniElement.getAttribute('data-uni-slug');

      if (uniSlug) {
        this.props.dispatch(actions.selectUniversity(uniSlug));
      } else {
        this.props.dispatch(actions.clearUniversity());
      }
    }
  }

  onMapContainerClick = (event) => {
    const element = event.target;

    MapComponent.handlePropertyClick(element);
    this.handleUniversityClick(element);
  };

  static setUniversityMarkerAnalytics(marker) {
    marker.on('click', (event) => {
      analyticsEvent(analyticsCategory, 'cardOpenUniversity', event.target.id);
    });
  }

  constructor(element, props = {}) {
    if (!props.getState || !props.dispatch || !props.subscribe) {
      throw new Error('You must specify props.getState, props.dispatch and props.subscribe');
    }

    super(element, props);
  }

  init() {
    // TODO: update trans namespace when microapp trans domains are added
    this.props.i18n.loadNamespaces(['map', 'property_detail_rooms'], () => {
      this.props.subscribe(this.propagateStoreChanges);
      this.addEventListeners();

      this.prevState = this.props.getState();
      this.propagateStoreChanges();
    });
  }

  destroy() {
    this.removeEventListeners();
  }

  addEventListeners() {
    this.element.addEventListener('click', this.onMapContainerClick);
  }

  removeEventListeners() {
    this.element.removeEventListener('click', this.onMapContainerClick);
  }

  propagateStoreChanges = () => {
    const state = this.props.getState();
    const scriptLoadedChanged = this.statePropertyChanged('scriptLoaded');
    const propertyFeaturesChanged = state.get('propertyFeatures')
      && !state.get('propertyFeatures').equals(this.prevState.get('propertyFeatures'));
    const universityFeaturesChanged = state.get('universityFeatures')
      && !state.get('universityFeatures').equals(this.prevState.get('universityFeatures'));
    const zoom = state.get('zoom');
    const zoomChanged = zoom && !zoom.equals(this.prevState.get('zoom'));

    if (scriptLoadedChanged) {
      this.initMap(state);
    }

    if (state.get('mapInitialised')) {
      if (propertyFeaturesChanged) {
        this.updatePropertiesLayer(state.get('propertyFeatures'));
      }

      if (universityFeaturesChanged) {
        this.updateUniversitiesLayer(state.get('universityFeatures'));
      }

      if (this.statePropertyChanged('selectedUniversitySlug')) {
        this.updateUniversitiesLayerFilter(state.get('selectedUniversitySlug'));
      }
    }

    if (zoomChanged) {
      this.adjustMapZoom(zoom);
    }

    if (this.statePropertyChanged('selectedPropertySlug')) {
      this.openSelectedPropertyPopup(state.get('selectedPropertySlug'));
    }

    this.prevState = state;

    if (scriptLoadedChanged) {
      this.props.dispatch(actions.setMapInitialised());
    }
  };

  statePropertyChanged(propertyName) {
    return this.props.getState().get(propertyName) !== this.prevState.get(propertyName);
  }

  initMap(state) {
    L.mapbox.accessToken = this.props.accessToken;
    this.map = L.mapbox.map(this.element, 'mapbox.streets', {
      attributionControl: false,
    });

    const propertiesData = state.get('propertyFeatures');
    const universitiesData = state.get('universityFeatures');

    if (propertiesData) {
      this.addPropertiesLayer(propertiesData);
    }

    if (universitiesData) {
      this.addUniversitiesLayer(universitiesData);
      this.updateUniversitiesLayerFilter(state.get('selectedUniversitySlug'));
    }
  }

  adjustMapZoom(zoom) {
    switch (zoom.get('type')) {
      case 'layer': {
        const layers = zoom.get('layers');
        let bounds = this.mapLayers[layers.first()].getBounds();

        layers.shift().forEach((layer) => {
          bounds = bounds.extend(this.mapLayers[layer].getBounds());
        });

        this.mapFitBoundsWithPadding(bounds);
        break;
      } case 'propAndUni': {
        const propertyMarker = getMarker(
          this.propertiesLayer,
          properties => properties.slug === zoom.getIn(['propAndUni', 'property'])
        );
        const uniMarker = getMarker(
          this.universitiesLayer,
          properties => properties.slug === zoom.getIn(['propAndUni', 'university'])
        );
        this.zoomMapToMarkers(propertyMarker, uniMarker);
        break;
      } case 'level': {
        this.map.setZoom(zoom.get('level'));
        break;
      } default: {
        return;
      }
    }
  }

  zoomMapToMarkers(markerA, markerB) {
    const bounds = L.latLngBounds(markerA.getLatLng(), markerB.getLatLng());
    this.mapFitBoundsWithPadding(bounds);
  }

  mapFitBoundsWithPadding(bounds) {
    this.map.fitBounds(bounds, {
      paddingTopLeft: isSmallScreen() ? L.point(74, 56) : L.point(82, 64),
      paddingBottomRight: isSmallScreen() ? L.point(74, 24) : L.point(82, 32),
    });
  }

  onBeforeTemplateRender() {
    handlebars.registerHelper('trans', key => this.props.i18n.t(key));
  }

  renderTemplate(template, props) {
    this.onBeforeTemplateRender();
    const result = template(props);
    onAfterTemplateRender();
    return result;
  }

  onPropertiesLayerAdd = (event) => {
    const marker = event.layer;
    const properties = marker.feature.properties;
    const price = formatCurrency(this.props.i18n, properties.price, properties.currency);
    const discountedPrice = formatCurrency(
      this.props.i18n,
      properties.discounted_price,
      properties.currency
    );
    const templateProperties = {
      ...properties,
      price,
      discounted_price: discountedPrice,
    };

    marker.id = properties.slug;
    this.buildPropertyIcon(marker, templateProperties);
    this.buildPropertyPopup(marker, templateProperties);
    MapComponent.setPropertyMarkerAnalytics(marker);
  };

  buildPropertyIcon(marker, templateProperties) {
    const renderedPin = this.renderTemplate(propertyPinTemplate, templateProperties);

    marker.setIcon(L.divIcon({
      className: 'property-pin',
      html: renderedPin,
      iconSize: null,
    }));

    marker.on('popupclose', () => {
      if (marker.feature.properties.viewed) {
        return;
      }

      // Reassigning here is the only way to let us know that the marker has
      // already been set to "viewed", so no need to continue. Please don't
      // try this at home!
      // eslint-disable-next-line no-param-reassign
      marker.feature.properties.viewed = true;

      marker.setIcon(L.divIcon({
        className: 'property-pin property-pin--viewed',
        html: renderedPin,
        iconSize: null,
      }));
    });
  }

  buildPropertyPopup(marker, templateProperties) {
    const renderedPopup = this.renderTemplate(propertyPopupTemplate, templateProperties);

    marker.bindPopup(renderedPopup, {
      closeButton: false,
      className: 'map-popup',
    });
  }

  onUniversitiesLayerAdd = (event) => {
    const marker = event.layer;
    const renderedPopup = this.renderTemplate(universityPopupTemplate, marker.feature.properties);

    marker.id = marker.feature.properties.slug;

    marker.setIcon(L.divIcon({
      className: 'university-pin',
      iconSize: null,
    }));

    marker.bindPopup(renderedPopup, {
      closeButton: false,
      className: 'map-popup map-popup--uni',
    });

    MapComponent.setUniversityMarkerAnalytics(marker);
  };

  addPropertiesLayer(layerData) {
    this.propertiesLayer = L.mapbox.featureLayer();
    this.propertiesLayer.on('layeradd', this.onPropertiesLayerAdd);
    this.updatePropertiesLayer(layerData);
    this.propertiesLayer.addTo(this.map);
  }

  addUniversitiesLayer(layerData) {
    this.universitiesLayer = L.mapbox.featureLayer();
    this.universitiesLayer.on('layeradd', this.onUniversitiesLayerAdd);
    this.updateUniversitiesLayer(layerData);
    this.universitiesLayer.addTo(this.map);
  }

  updatePropertiesLayer(layerData) {
    this.propertiesLayer.setGeoJSON(layerData.toJS());
  }

  updateUniversitiesLayer(layerData) {
    this.universitiesLayer.setGeoJSON(layerData.toJS());
  }

  openSelectedPropertyPopup(selectedPropertySlug) {
    const propertyMarker = getMarker(
      this.propertiesLayer,
      properties => properties.slug === selectedPropertySlug
    );
    propertyMarker.openPopup();
  }

  updateUniversitiesLayerFilter(selectedUniSlug) {
    if (selectedUniSlug) {
      this.addSelectedUniLayerFilter(selectedUniSlug);
    } else {
      this.removeSelectedUniLayerFilter();
    }
  }

  addSelectedUniLayerFilter(selectedUniSlug) {
    this.universitiesLayer.setFilter(feature => feature.properties.slug === selectedUniSlug);
    const marker = getMarker(
      this.universitiesLayer,
      properties => properties.slug === selectedUniSlug
    );
    if (!marker) {
      return;
    }

    const renderedPopup = this.renderTemplate(universityPopupTemplate, {
      ...marker.feature.properties,
      selected: true,
    });
    marker.setIcon(L.divIcon({
      className: 'university-pin-selected',
      iconSize: null,
    }));
    marker.setPopupContent(renderedPopup);
    marker.setZIndexOffset(zIndexes['z-on-top']);
  }

  removeSelectedUniLayerFilter() {
    this.universitiesLayer.setFilter(() => true);
  }

  get propertiesLayer() {
    return this.mapLayers.properties;
  }

  set propertiesLayer(value) {
    this.mapLayers.properties = value;
  }

  get universitiesLayer() {
    return this.mapLayers.universities;
  }

  set universitiesLayer(value) {
    this.mapLayers.universities = value;
  }
}

function onAfterTemplateRender() {
  handlebars.unregisterHelper('trans');
}

function getMarker(layer, matches) {
  let matchingMarker;

  layer.eachLayer((marker) => {
    if (matches(marker.feature.properties)) {
      matchingMarker = marker;
    }
  });

  return matchingMarker;
}



