import { Promise, all as promiseAll } from 'rsvp';
import axios from '~global-js/helpers/axios';
import queryString from 'query-string';
import { view, event } from '~global-js/helpers/tracking';
import Enum from '~global-js/helpers/enum';
import importScript from '~global-js/helpers/import-script';
import importStyle from '~global-js/helpers/import-style';
import { endPoints, paths } from '~global-js/settings/paths';
import { getLocale } from '~global-js/helpers/locale';
import analyticsCategory from '~components/map/js/settings.js';

const xhrSettings = {
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
    'Accept-Language': getLocale(),
  },
};

export const asyncStateTypes = new Enum(
  'IDLE',
  'FETCHING',
  'ERROR'
);

export const actionTypes = new Enum(
  'MAP_SET_PROPERTY_DATA',
  'MAP_SET_UNIVERSITY_DATA',
  'MAP_SET_ASYNC_STATE',
  'MAP_SHOW_MAP',
  'MAP_HIDE_MAP',
  'MAP_SET_SCRIPT_LOADED',
  'MAP_SET_MAP_INITIALISED',
  'MAP_SELECT_PROPERTY',
  'MAP_SELECT_UNIVERSITY',
  'MAP_CLEAR_UNIVERSITY',
  'MAP_SET_FILTERS',
  'MAP_ZOOM_TO_FIT_LAYERS',
  'MAP_ZOOM_TO_PROPERTY_AND_UNI',
  'MAP_ZOOM_TO_LEVEL'
);

export function clickViewMap(filters) {
  return dispatch =>
    new Promise((resolve, reject) => {
      dispatch(showMap());
      dispatch(setFilters(filters));
      dispatch(initMap())
        .then(() => {
          dispatch(zoomToAllPropertiesAndUnis());
          resolve();
        })
        .catch(() => {
          reject();
        });
    });
}


export function clickHideMap() {
  event(analyticsCategory, 'closeMap');
  view();

  return {
    type: actionTypes.MAP_HIDE_MAP,
    payload: {},
  };
}

export function clickViewPropertyOnMap(propertySlug, filters) {
  return (dispatch, getState) =>
    new Promise((resolve, reject) => {
      dispatch(showMap());
      dispatch(setFilters(filters));
      dispatch(initMap())
        .then(() => {
          const mapState = getState().map;
          const universitySlug = mapState.get('selectedUniversitySlug');

          if (universitySlug) {
            dispatch(zoomToPropertyAndUniversity(propertySlug, universitySlug));
          } else {
            dispatch(zoomToAllPropertiesAndUnis());
          }
          dispatch(selectProperty(propertySlug));
          resolve();
        })
        .catch(() => {
          reject();
        });
    });
}

export function setFilters(filters) {
  return (dispatch) => {
    if (!filters.country_slug || !filters.city_slug) {
      throw new Error('microapp-frontend-base:map:actions: country_slug or city_slug missing from filters');
    }

    dispatch(setFiltersObject(filters));
    const uniSlug = filters.university_slug;

    if (uniSlug) {
      dispatch(selectUniversity(uniSlug));
    }
  };
}

export function getMapsData() {
  return dispatch =>
    new Promise((resolve, reject) => {
      promiseAll([
        dispatch(getProperties()),
        dispatch(getUniversities()),
      ]).then(() => {
        resolve();
      }).catch(() => {
        reject();
      });
    });
}

/**
 * @private
 */
export function setPropertyData(response) {
  return {
    type: actionTypes.MAP_SET_PROPERTY_DATA,
    payload: {
      response,
    },
  };
}

/**
 * @private
 */
export function setUniversityData(response) {
  return {
    type: actionTypes.MAP_SET_UNIVERSITY_DATA,
    payload: {
      response,
    },
  };
}

/**
 * @private
 */
export function setAsyncState(asyncState) {
  return {
    type: actionTypes.MAP_SET_ASYNC_STATE,
    payload: {
      asyncState,
    },
  };
}

/**
 * Action for internal use. Updates the `mapVisible` property.
 * @private
 */
export function showMap() {
  view('map');

  return {
    type: actionTypes.MAP_SHOW_MAP,
    payload: {},
  };
}

/**
 * @private
 */
export function setScriptLoaded() {
  return {
    type: actionTypes.MAP_SET_SCRIPT_LOADED,
    payload: {},
  };
}

/**
 * @private
 */
export function setMapInitialised() {
  return {
    type: actionTypes.MAP_SET_MAP_INITIALISED,
    payload: {},
  };
}

/**
 * @private
 */
export function selectProperty(propertySlug) {
  return {
    type: actionTypes.MAP_SELECT_PROPERTY,
    payload: {
      propertySlug,
    },
  };
}

/**
 * @private
 */
export function selectUniversity(universitySlug) {
  event(analyticsCategory, 'setUniversity', universitySlug);

  return {
    type: actionTypes.MAP_SELECT_UNIVERSITY,
    payload: {
      universitySlug,
    },
  };
}

/**
 * @private
 */
export function clearUniversity() {
  return {
    type: actionTypes.MAP_CLEAR_UNIVERSITY,
  };
}

/**
 * @private
 */
export function setFiltersObject(filters) {
  return {
    type: actionTypes.MAP_SET_FILTERS,
    payload: {
      filters,
    },
  };
}

/**
 * @private
 */
export function zoomToAllPropertiesAndUnis() {
  return zoomToFitLayers(['properties', 'universities']);
}

/**
 * @private
 */
export function zoomToFitLayers(layers) {
  return {
    type: actionTypes.MAP_ZOOM_TO_FIT_LAYERS,
    payload: {
      zoomType: 'layer',
      layers,
    },
  };
}

/**
 * @private
 */
export function zoomToPropertyAndUniversity(propertySlug, universitySlug) {
  return {
    type: actionTypes.MAP_ZOOM_TO_PROPERTY_AND_UNI,
    payload: {
      zoomType: 'propAndUni',
      propertySlug,
      universitySlug,
    },
  };
}

/**
 * @private
 */
export function zoomToLevel(level) {
  return {
    type: actionTypes.MAP_ZOOM_TO_LEVEL,
    payload: {
      zoomType: 'level',
      level,
    },
  };
}

/**
 * @private
 */
export function initMap() {
  return (dispatch, getState) =>
    new Promise((resolve, reject) => {
      const mapState = getState().map;
      dispatch(setAsyncState(asyncStateTypes.FETCHING));

      if (!mapState.get('scriptLoaded')) {
        importStyle(paths.leafletStyles);
        promiseAll([dispatch(getMapsData()), importScript(paths.mapboxScript)])
          .then(() => {
            dispatch(setScriptLoaded());
            dispatch(setAsyncState(asyncStateTypes.IDLE));
            resolve();
          })
          .catch(() => {
            dispatch(setAsyncState(asyncStateTypes.ERROR));
            reject();
          });
      } else {
        dispatch(getMapsData())
          .then(() => {
            dispatch(setAsyncState(asyncStateTypes.IDLE));
            resolve();
          })
          .catch(() => {
            dispatch(setAsyncState(asyncStateTypes.ERROR));
            reject();
          });
      }
    });
}

/**
 * @private
 */
export function getProperties() {
  return (dispatch, getState) =>
    new Promise((resolve, reject) => {
      const mapState = getState().map;
      const endPoint = getEndPoint(endPoints.propertiesSearch, {
        page_size: mapState.get('maximumPageSize'),
        ...mapState.get('filters').toJS(),
      });

      axios.get(endPoint, xhrSettings)
        .then((response) => {
          dispatch(setPropertyData(response.data));
          resolve();
        }).catch(() => {
          reject();
        });
    });
}

/**
 * @private
 */
export function getUniversities() {
  return (dispatch, getState) =>
    new Promise((resolve, reject) => {
      const mapState = getState().map;
      const endPoint = getEndPoint(endPoints.universitiesSearch, {
        page_size: 5000,
        ...mapState.get('filters').toJS(),
      });

      axios.get(endPoint, xhrSettings)
        .then((response) => {
          dispatch(setUniversityData(response.data));
          resolve();
        }).catch(() => {
          reject();
        });
    });
}

export function getEndPoint(basePath, filters) {
  const query = queryString.stringify(filters);
  return query ? `${basePath}?${query}` : basePath;
}



