import Immutable from 'immutable';
import { actionTypes, asyncStateTypes } from '~components/map/js/actions';
import Enum from '~global-js/helpers/enum';
import generatePath from '~global-js/helpers/router';
import getImageUrl from '~global-js/helpers/image';

export const MAP_DEFAULT_STATE = Immutable.Map({
  scriptLoaded: false,
  mapInitialised: false,
  propertyFeatures: null,
  universityFeatures: null,
  asyncState: asyncStateTypes.IDLE,
  mapVisible: false,
  zeroState: false,
  selectedPropertySlug: '',
  selectedUniversitySlug: '',
  maximumPageSize: 1000,
  filters: {},
  zoom: null,
});

export const defaultImageFilter = 'hero_banner_mobile';
export const propertyImageFilter = 'property_preview_mobile';

function getEmptyFeatureLayer() {
  return {
    type: 'FeatureCollection',
    features: [],
  };
}

export function mapReducer(state = MAP_DEFAULT_STATE, action) {
  switch (action.type) {
    case actionTypes.MAP_SET_PROPERTY_DATA: {
      const propertyFeatures = getFeaturesFromPropertyResponse(
        action.payload.response,
        state.get('filters')
      );

      return state.merge({
        propertyFeatures,
        zeroState: propertyFeatures.features.length === 0,
      });
    } case actionTypes.MAP_SET_UNIVERSITY_DATA: {
      const universityFeatures = getFeaturesFromUniversityResponse(action.payload.response);

      return state.merge({
        universityFeatures,
      });
    } case actionTypes.MAP_SET_ASYNC_STATE: {
      return state.merge({
        asyncState: action.payload.asyncState,
      });
    } case actionTypes.MAP_SHOW_MAP: {
      return state.merge({
        mapVisible: true,
      });
    } case actionTypes.MAP_HIDE_MAP: {
      return state.merge({
        mapVisible: false,
        filters: {},
        propertyFeatures: getEmptyFeatureLayer(),
        universityFeatures: getEmptyFeatureLayer(),
      });
    } case actionTypes.MAP_SET_SCRIPT_LOADED: {
      return state.merge({
        scriptLoaded: true,
      });
    } case actionTypes.MAP_SET_MAP_INITIALISED: {
      return state.merge({
        mapInitialised: true,
      });
    } case actionTypes.MAP_SELECT_PROPERTY: {
      return state.merge({
        selectedPropertySlug: action.payload.propertySlug,
      });
    } case actionTypes.MAP_SELECT_UNIVERSITY: {
      return state.merge({
        selectedUniversitySlug: action.payload.universitySlug,
      });
    } case actionTypes.MAP_CLEAR_UNIVERSITY: {
      return state.merge({
        selectedUniversitySlug: '',
      });
    } case actionTypes.MAP_SET_FILTERS: {
      return state.merge({
        filters: action.payload.filters,
      });
    } case actionTypes.MAP_ZOOM_TO_FIT_LAYERS: {
      return state.mergeDeep({
        zoom: {
          type: action.payload.zoomType,
          layers: action.payload.layers,
        },
      });
    } case actionTypes.MAP_ZOOM_TO_PROPERTY_AND_UNI: {
      return state.mergeDeep({
        zoom: {
          type: action.payload.zoomType,
          propAndUni: {
            property: action.payload.propertySlug,
            university: action.payload.universitySlug,
          },
        },
      });
    } case actionTypes.MAP_ZOOM_TO_LEVEL: {
      return state.mergeDeep({
        zoom: {
          type: action.payload.zoomType,
          level: action.payload.level,
        },
      });
    } default: {
      if (!Immutable.Map.isMap(state)) {
        const newState = MAP_DEFAULT_STATE.merge(Immutable.fromJS(state));

        return newState.merge({
          zeroState: state.getIn(['mapData', 'properties', 'features']).count() === 0,
        });
      }

      return state;
    }
  }
}

const listingStates = new Enum(
  'active',
  'available',
  'available_with_price',
  'coming_soon',
  'sold_out',
);

function getSoldOutState(state) {
  return state === listingStates.sold_out;
}

function getEnquireForPriceState(state) {
  return state === listingStates.available || state === listingStates.coming_soon;
}

function getRegisterInterestState(state) {
  return !state;
}

function getFeaturesFromPropertyResponse(response, filters) {
  const result = {
    type: 'FeatureCollection',
    features: [],
  };

  response.properties.forEach((property) => {
    const link = generatePath('property_detail', {
      countrySlug: filters.get('country_slug'),
      citySlug: filters.get('city_slug'),
      propertySlug: property.slug,
    });

    const feature = {
      type: 'Feature',
      geometry: {
        type: 'Point',
        coordinates: [
          property.longitude,
          property.latitude,
        ],
      },
      properties: {
        name: property.name,
        image: getFormattedHeroImage(property.hero_image, propertyImageFilter),
        slug: property.slug,
        link,
        sold_out: getSoldOutState(property.state),
        enquire_for_price: getEnquireForPriceState(property.state),
        register_interest: getRegisterInterestState(property.state),
        price: property.from_price,
        discounted_price: property.discounted_from_price,
        currency: property.currency,
      },
    };

    result.features.push(feature);
  });

  return result;
}

export function getFeaturesFromUniversityResponse(response) {
  const result = {
    type: 'FeatureCollection',
    features: [],
  };

  response.universities.forEach((university) => {
    const feature = {
      type: 'Feature',
      geometry: {
        type: 'Point',
        coordinates: [
          university.longitude,
          university.latitude,
        ],
      },
      properties: {
        name: university.name,
        image: getFormattedHeroImage(university.hero_image),
        slug: university.slug,
      },
    };

    result.features.push(feature);
  });

  return result;
}

function getFormattedHeroImage(imageData, imageFilter = defaultImageFilter) {
  if (!imageData) {
    return null;
  }

  return {
    alt_text: imageData.alt_text,
    src: getImageUrl(imageData.source, imageFilter),
  };
}



